import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SellerSignupComponent } from './seller-signup/seller-signup.component'
import { SellerLoginComponent } from './seller-login/seller-login.component'
import { HomeComponent } from './home/home.component';
import { AuthGuard } from './util/auth.gaurd';
import { DashboardComponent } from './dashboard/dashboard.component';
import { AssetComponent } from './asset/asset.component';
import { AssetCreateComponent } from './asset-create/asset-create.component';
import { AssetShowComponent } from './asset-show/asset-show.component';
import { AssetUpdateComponent } from './asset-update/asset-update.component';
import { AssetViewComponent } from './asset-view/asset-view.component';
import { AdminDashboardComponent } from './admin/admin-dashboard/admin-dashboard.component';
import { AdminComponent } from './admin/admin.component';
import { AdminLoginComponent } from './admin/admin-login/admin-login.component';
import { AdminSellerComponent } from './admin/admin-seller/admin-seller.component';
import { AdminSellerIndexComponent } from './admin/admin-seller-index/admin-seller-index.component';
import { AdminSellerShowComponent } from './admin/admin-seller-show/admin-seller-show.component';
import { AdminAssetComponent } from './admin/admin-asset/admin-asset.component';
import { AdminAssetIndexComponent } from './admin/admin-asset-index/admin-asset-index.component';
import { AdminAssetShowComponent } from './admin/admin-asset-show/admin-asset-show.component';

const routes: Routes = [
  { path: '', component: HomeComponent},
  { path: 'signup', component: SellerSignupComponent },
  { path: 'login', component: SellerLoginComponent },
  { path: 'dashboard', component: DashboardComponent, canActivate: [AuthGuard] },
  { path: 'asset', component: AssetComponent, canActivate: [AuthGuard] },

  { path: 'asset', component: AssetComponent, 
    children: [
      { path: 'create', component: AssetCreateComponent },
      { path: 'show/:id', component: AssetShowComponent },
      { path: 'update/:id', component: AssetUpdateComponent }
    ]
   },

  { path: 'admin', component: AdminComponent, 
    children: [
      { path: '', component: AdminLoginComponent },
      { path: 'dashboard', component: AdminDashboardComponent },

      { path: 'seller', component: AdminSellerComponent, 
        children: [
          { path: '', component: AdminSellerIndexComponent },
          { path: 'show/:id', component: AdminSellerShowComponent }
        ]
      },
      { path: 'asset', component: AdminAssetComponent, 
        children: [
          { path: '', component: AdminAssetIndexComponent },
          { path: 'show/:id', component: AdminAssetShowComponent }
        ]
      }
    ]
   },

  { path: 'asset-view/:id', component: AssetViewComponent },

  // otherwise redirect to home
  { path: '**', redirectTo: '' }
  
  // { path: '', redirectTo: 'home', pathMatch: 'full' },
  // { path: '**', component: ErrorComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
