import { Component, OnInit, ViewChild } from '@angular/core';
import { AdminService } from '../admin.service';
import {MatPaginator, MatTableDataSource, MatSort, Sort, MatMenuTrigger} from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import { Router, ActivatedRoute } from '@angular/router';

export interface AssetListElement {
  isActive: boolean;
  owner: string;
  description: string;
  houseNo: string;
  address: string;
  city: string;
  state: string;
  zip: number;
  lat: number;
  lng: number;
  numBedrooms: number;
  numBathrooms: number;
  yearBuilt: number;
  squareFootage: number;
  occupancyStatus: string;
  lotSize: number;
  exteriorDetails: string;
  foundationDetails: string;
  roofDetails: string;
  lotDetails: string;
  garageDetails: string;
  parkingDetails: string;
  photos: Array<any>
}



const initialSelection = [];
const allowMultiSelect = true;

@Component({
  selector: 'app-admin-asset-index',
  templateUrl: './admin-asset-index.component.html',
  styleUrls: ['./admin-asset-index.component.scss']
})
export class AdminAssetIndexComponent implements OnInit {

  selection = new SelectionModel<AssetListElement>(allowMultiSelect, initialSelection);

  public displayedColumns: string[] = ['select', 'status', 'houseNo', 'address',  'city', 'zip', 'state', 'star'];

  public dataSource: MatTableDataSource<AssetListElement>;
  sortedData: AssetListElement[];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatMenuTrigger) trigger: MatMenuTrigger;


  public assetList: Array<any>
  

  private adminService:AdminService;
  private router:Router;
  private activatedRoute:ActivatedRoute;

  constructor(adminService: AdminService, router:Router, activatedRoute:ActivatedRoute){
    this.adminService = adminService;
    this.router = router;
    this.activatedRoute = activatedRoute;
  }

  ngOnInit() {
    this.adminService.getAssetList().subscribe(
      data => {
        console.log(data)
        this.assetList = data.content;
        // for(let i=0; i < this.assetList.length; i++){
        //   this.assetList[i]['position'] = i+1;
        // }
        this.dataSource = new MatTableDataSource<AssetListElement>(this.assetList);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;

      },
      error => {
        console.log(error)
      }
    )
  }

  showItem(data:any){
    let url  = `/admin/asset/show/${data._id}`
    this.router.navigateByUrl(url).then(nav => {
      console.log(nav); // true if navigation is successful
    }, err => {
      console.log(err) // when there's an error
    });
  }

  

  someMethod() {
    this.trigger.openMenu();
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  /** Whether the number of selected elements matches the total number of rows. */
isAllSelected() {
  const numSelected = this.selection.selected.length;
  const numRows = this.dataSource.data.length;
  return numSelected == numRows;
}

/** Selects all rows if they are not all selected; otherwise clear selection. */
masterToggle() {
  this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
}


}
