import { Component, OnInit, ViewChild } from '@angular/core';
import { AdminService } from '../admin.service';
import {MatPaginator, MatTableDataSource, MatSort, Sort, MatMenuTrigger} from '@angular/material';
import { SelectionModel } from '@angular/cdk/collections';
import { Router, ActivatedRoute } from '@angular/router';

export interface SellerListElement {
  status: boolean;
  firstName: string;
  lastName: string;
  phone: string;
  address: string;
  city: string;
  zip: string;
}

const initialSelection = [];
const allowMultiSelect = true;

@Component({
  selector: 'app-admin-seller-index',
  templateUrl: './admin-seller-index.component.html',
  styleUrls: ['./admin-seller-index.component.scss']
})
export class AdminSellerIndexComponent implements OnInit {

  
  selection = new SelectionModel<SellerListElement>(allowMultiSelect, initialSelection);

  public displayedColumns: string[] = ['select', 'status', 'firstName', 'lastName',  'phone', 'address', 'city', 'zip', 'star'];
  public dataSource: MatTableDataSource<SellerListElement>;
  sortedData: SellerListElement[];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort) sort: MatSort;
  @ViewChild(MatMenuTrigger) trigger: MatMenuTrigger;


  public sellerList: Array<any>
  

  private adminService:AdminService;
  private router:Router;
  private activatedRoute:ActivatedRoute;

  constructor(adminService: AdminService, router:Router, activatedRoute:ActivatedRoute) {
    this.adminService = adminService;
    this.router = router;
    this.activatedRoute = activatedRoute;
   }

  ngOnInit() {

    this.adminService.getSellerList().subscribe(
      data => {
        console.log(data)
        this.sellerList = data.content;
       
        this.dataSource = new MatTableDataSource<SellerListElement>(this.sellerList);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;

      },
      error => {
        console.log(error)
      }
    )
  }
  showItem(data:any){
    let url  = `/admin/seller/show/${data._id}`
    this.router.navigateByUrl(url).then(nav => {
      console.log(nav); // true if navigation is successful
    }, err => {
      console.log(err) // when there's an error
    });
  }

  

  someMethod() {
    this.trigger.openMenu();
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  /** Whether the number of selected elements matches the total number of rows. */
isAllSelected() {
  const numSelected = this.selection.selected.length;
  const numRows = this.dataSource.data.length;
  return numSelected == numRows;
}

/** Selects all rows if they are not all selected; otherwise clear selection. */
masterToggle() {
  this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
}



  sortData(sort: Sort) {
    const data = this.sellerList.slice();
    if (!sort.active || sort.direction === '') {
      this.sortedData = data;
      this.dataSource = new MatTableDataSource<SellerListElement>(data);
      return;
    }

    this.sortedData = data.sort((a, b) => {
      const isAsc = sort.direction === 'asc';
      switch (sort.active) {
        case 'position': return compare(a.name, b.name, isAsc);
        case 'firstName': return compare(a.calories, b.calories, isAsc);
        case 'phone': return compare(a.fat, b.fat, isAsc);
        case 'city': return compare(a.carbs, b.carbs, isAsc);
        default: return 0;
      }
    });
  }
}

function compare(a: number | string, b: number | string, isAsc: boolean) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}


