import { Component, OnInit, NgZone } from '@angular/core';

import {FormGroup, FormControl, FormGroupDirective, NgForm, Validators, FormBuilder} from '@angular/forms';
import {ErrorStateMatcher} from '@angular/material/core';

import {PaperFormErrorStateMatcher} from '../util/paper-form-error-state-matcher'
import {PasswordMatchErrorStateMatcher} from '../util/password-match-error-state-matcher'
import { AppService } from '../app-service';
import { ActivatedRoute, Router } from '@angular/router';



@Component({
  selector: 'app-seller-signup',
  templateUrl: './seller-signup.component.html',
  styleUrls: ['./seller-signup.component.scss']
})
export class SellerSignupComponent implements OnInit {

  public addrKeys: string[];
  public addr: object;
  private formBuilder:FormBuilder
  public matcher:ErrorStateMatcher
  public matcherForPassword:ErrorStateMatcher
  public paperForm:FormGroup
  

  //Method to be invoked everytime we receive a new instance 
  //of the address object from the onSelect event emitter.
  setAddress(addrObj) {
    //We are wrapping this in a NgZone to reflect the changes
    //to the object in the DOM.
    this.zone.run(() => {
      this.addr = addrObj;
      this.addrKeys = Object.keys(addrObj);
      console.log(this.addr)
      console.log(this.addrKeys)
    });
  }

  constructor(
    private zone: NgZone, 
    private appService:AppService,
    private route: ActivatedRoute,
    private router: Router,
    ) {
    this.formBuilder = new FormBuilder();

    this.matcher = new PaperFormErrorStateMatcher();
    this.matcherForPassword = new PasswordMatchErrorStateMatcher();

    this.paperForm = this.formBuilder.group({
      firstName: ['', [ Validators.required ]],
      lastName: ['', [ Validators.required ]],
      address: ['', [ Validators.required ]],

      username: ['', [ Validators.required, Validators.email ]],

      password: ['', [ Validators.required ]],
      passwordConfirmation: [''],
      phone: ['', [ Validators.required ]],
      homePhone: ['', [ Validators.required ]]
    }, {validator: this.checkPasswords })
    
  }

  


  ngOnInit() {
    // reset login status
    this.appService.logout();
  }


  

  onSubmit() {
    // TODO: Use EventEmitter with form value
    console.warn(this.paperForm.value);
    let data:any = this.paperForm.value;
    this.appService.signup(data)
      .subscribe(
        result => {
          console.log(result);
          this.router.navigate(['dashboard']);
        }, 
        error => {
          console.log(error);
        }
      )
  }

  
  checkPasswords(group: FormGroup) { // here we have the 'passwords' group
    // console.log(group)
    let pass = group.controls.password.value;
    let confirmPass = group.controls.passwordConfirmation.value;

    return pass === confirmPass ? null : { notSame: true }     
  }

}
