import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { AdminService } from '../admin.service';
import { environment } from 'src/environments/environment.prod';

@Component({
  selector: 'app-admin-seller-show',
  templateUrl: './admin-seller-show.component.html',
  styleUrls: ['./admin-seller-show.component.scss']
})
export class AdminSellerShowComponent implements OnInit {

  private router:Router;
  private activatedRoute:ActivatedRoute;
  private adminService:AdminService;
  private itemId:string;

  public seller:any;
  public imagePath: string = environment.imagePath;

  constructor( adminService:AdminService, router:Router, activatedRoute:ActivatedRoute ) {
    this.adminService = adminService;
    this.router = router;
    this.activatedRoute = activatedRoute;
  }

  ngOnInit() {
    this.itemId = this.activatedRoute.snapshot.params.id
    this.adminService.getSellerDetails(this.itemId).subscribe(
      data => {
        console.log(data)
        this.seller = data.content;
      },
      error => {
        console.log(error)
      }
    )
  }
  showAsset(data:any){
    let url = `admin/asset/show/${data._id}`
    this.router.navigateByUrl(url)
  }

}
