import { Component, OnInit, NgZone, ViewChild } from '@angular/core';
import { AppService } from '../app-service';
import { ActivatedRoute, Router } from '@angular/router';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { PaperFormErrorStateMatcher } from '../util/paper-form-error-state-matcher';
import { ErrorStateMatcher } from '@angular/material';
import { environment } from '../../environments/environment';

import { NgxImageGalleryComponent, GALLERY_IMAGE, GALLERY_CONF } from "ngx-image-gallery";

export interface BedCount {
  value: number;
  viewValue: string;
}

export interface BathCount {
  value: number;
  viewValue: string;
}

export interface Year {
  value: number;
  viewValue: string;
}


@Component({
  selector: 'app-asset-create',
  templateUrl: './asset-create.component.html',
  styleUrls: ['./asset-create.component.scss']
})
export class AssetCreateComponent implements OnInit {

  public addrKeys: string[];
  public addr: object;
  private formBuilder:FormBuilder
  public matcher:ErrorStateMatcher
  public paperForm:FormGroup

  public imagePath: string = environment.imagePath;

  // get reference to gallery component
  @ViewChild(NgxImageGalleryComponent) 
  ngxImageGallery: NgxImageGalleryComponent;
  
  // gallery configuration
  conf: GALLERY_CONF = {
    imageOffset: '0px',
    showDeleteControl: false,
    showImageTitle: false,
    inline: false,
    
  };
	
  // gallery images
  images: GALLERY_IMAGE[] = [ ];

  // METHODS
  // open gallery
  openGallery(index: number = 0) {
    this.ngxImageGallery.open(index);
  }
	
  // close gallery
  closeGallery() {
    this.ngxImageGallery.close();
  }
	
  // set new active(visible) image in gallery
  newImage(index: number = 0) {
    this.ngxImageGallery.setActiveImage(index);
  }
	
  // next image in gallery
  nextImage(index: number = 0) {
    // this.ngxImageGallery.next(index);
    this.ngxImageGallery.next();
  }
	
  // prev image in gallery
  prevImage(index: number = 0) {
    // this.ngxImageGallery.prev(index);
    this.ngxImageGallery.prev();
  }
	
  /**************************************************/
	
  // EVENTS
  // callback on gallery opened
  galleryOpened(index) {
    console.info('Gallery opened at index ', index);
  }

  // callback on gallery closed
  galleryClosed() {
    console.info('Gallery closed.');
  }

  // callback on gallery image clicked
  galleryImageClicked(index) {
    console.info('Gallery image clicked with index ', index);
  }
  
  // callback on gallery image changed
  galleryImageChanged(index) {
    console.info('Gallery image changed to index ', index);
  }

  // callback on user clicked delete button
  deleteImage(index) {
    console.info('Delete image at index ', index);
  }

  // =================================================== //

  //Method to be invoked everytime we receive a new instance 
  //of the address object from the onSelect event emitter.
  setAddress(addrObj) {
    //We are wrapping this in a NgZone to reflect the changes
    //to the object in the DOM.
    this.zone.run(() => {
      this.addr = addrObj;
      this.addrKeys = Object.keys(addrObj);
      console.log(this.addr)
      console.log(this.addrKeys)
    });
  }

  public bedCounts:BedCount[];
  public bathCounts:BedCount[];
  public years:Year[];

  constructor(
    private zone: NgZone, 
    private appService:AppService,
    private route: ActivatedRoute,
    private router: Router,
    ) {


      this.bedCounts= [
        { value: 1, viewValue: '1' },
        { value: 2, viewValue: '2' },
        { value: 3, viewValue: '3' },
        { value: 4, viewValue: '4' },
        { value: 5, viewValue: '5' }
        
      ];

      this.bathCounts= [
        { value: 1, viewValue: '1' },
        { value: 2, viewValue: '2' },
        { value: 3, viewValue: '3' },
        { value: 4, viewValue: '4' },
        { value: 5, viewValue: '5' }
        
      ];

      this.years = []

      for(let i:number = 1940; i < 2019; i++){
        this.years.push({ value: i, viewValue: i.toString() })
      }

    this.formBuilder = new FormBuilder();

    this.matcher = new PaperFormErrorStateMatcher();
    

    this.paperForm = this.formBuilder.group({
      houseNo: ['', [ Validators.required ]],
      address: ['', [ Validators.required ]],
      description: ['', [ Validators.required ]],
      numBedrooms: ['', [ Validators.required ]],
      numBathrooms: ['', [ Validators.required ]],
      yearBuilt: ['', [ Validators.required ]],
      squareFootage: ['', [ Validators.required ]],
      lotSize: ['', [ Validators.required ]],
      exteriorDetails: ['', [  ]],
      foundationDetails: ['', [  ]],
      roofDetails: ['', [  ]],
      lotDetails: ['', [  ]],
      garageDetails: ['', [ ]],
      parkingDetails: ['', [  ]]
      
    })
  }

  ngOnInit() {
  }

  updateImageList(data) {
    console.log("data")
    console.log(data)
    let imgList = Array.from(this.images, (x: any) => {
      return { 
        url:  environment.imagePath + '/' + x.filename, 
        title: '', 
        altText: '', 
        thumbnailUrl: environment.imagePath + '/' + x.filename
      }
    })
    this.images = this.images.concat(imgList)
  }



}
