import { Component, Output, EventEmitter } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material';
import { DialogComponent } from './dialog/dialog.component';
import { UploadService } from './upload.service';
import { filter } from 'rxjs/operators';
import { environment } from '../../environments/environment';


@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.scss']
})
export class UploadComponent {

  private dialogRef: MatDialogRef<DialogComponent>

  @Output()
  public onUploadComplete:EventEmitter<any> = new EventEmitter<any>();

  constructor(public dialog: MatDialog, public uploadService: UploadService) {}

  public openUploadDialog() {
    this.dialogRef = this.dialog.open(DialogComponent, { width: '50%', height: '50%' });

    this.dialogRef.afterClosed().subscribe((data) => {
      console.log('data')
      console.log(data)
      let list = Array.from( data, (x:any) => {
        return { 
          url:  x.filename, 
          title: '', 
          altText: '', 
          thumbnailUrl: x.filename
        }
      })
      
      this.onUploadComplete.emit(list)

    })

  }
}
